export default {
    data () {
        return {
            showId:false,
            showPeriods:false,
            showToast:false,
            showAlert:false,
            playWay:"重庆时时彩  定位-万码",
            playlist:["重庆时时彩  定位-万码"],
            period:"012 - 013期",
            periodList:["012 - 013期","012 - 014期","012 - 015期"]
        }
    },
    methods : {
        // 周期选择框
       isShow () {
           this.showPeriods=!this.showPeriods;
       },
        // 选择周期
       checkPeriod (item) {
          this.showPeriods=false;
          this.period=item;
       },
        // 确认发布
       toPlan () {
        //    this.showToast=true;
              this.showAlert=true;
        
       }
    }
}



